import MoviesForum from './MoviesForum/MoviesForum';
import MovieCard from './MovieCard/MovieCard';

import { getAllMoviesArticles } from '../../../utils/MainRequests';

import Loader from '../../Common/Loader/Loader';
import useData from '../../CustomHooks/useData';

function Home() {
    const [movies, isLoaded] = useData([], getAllMoviesArticles, [], true);
    
    return (
        <>
            <MoviesForum />
            <section>
                <div>
                    <h1 style={{
                        fontSize: 35,
                        fontWeight: 500,
                        fontFamily: 'Courier New',
                        textAlign: 'center',
                        marginTop: 40
                    }}>Movie Articles</h1>
                    <div className="card-wrapper">
                        {
                            !isLoaded ? <Loader /> : movies.map((movieProps, keyIndex) => <MovieCard key={keyIndex} {...movieProps} />)
                        }
                    </div>
                </div>
            </section>
        </>
    );
}

export default Home;