import { useContext } from 'react';
import style from '../Decorate.module.css';

import { getSpecificMovieArticle, editMovieArticle } from '../../../../utils/MainRequests';
import { UserContext } from '../../../../Context/UserContext';
import useData from '../../../CustomHooks/useData';

function Edit({
    match,
    history
}) {
    const { currentUserData } = useContext(UserContext);
    const movieArticle = useData({}, getSpecificMovieArticle, [match.params.movieId], false);

    const onEditMovieArticleSubmitHandler = async (e) => {
        e.preventDefault();
        const { movieTitle, movieDescription, movieImage } = e.target;
        await editMovieArticle(movieTitle.value, movieDescription.value, movieImage.value, match.params.movieId, currentUserData.authorizationToken);
        history.push(`/movies/details/${match.params.movieId}`);
    }

    return (
        <section className={style.createPage}>
            <div className={style.createFormWrapper}>
                <h2 className={style.createFormTitle}>EDIT MOVIE ARTICLE</h2>
                <form onSubmit={onEditMovieArticleSubmitHandler}>
                    <input className={style.movieTitle}
                        type="text"
                        name="movieTitle"
                        placeholder="ENTER MOVIE TITLE"
                        defaultValue={movieArticle.title}
                    />
                    <textarea className={style.movieDescription}
                        type="text"
                        name="movieDescription"
                        placeholder="ENTER MOVIE DESCRIPTION"
                        rows="4"
                        columns="50"
                        defaultValue={movieArticle.description}
                    >
                    </textarea>
                    <input className={style.movieImageURL}
                        type="text"
                        name="movieImage"
                        placeholder="ENTER MOVIE IMAGE URL"
                        defaultValue={movieArticle.img}
                    />
                    <button className={style.createMovieBtn} type="submit">EDIT</button>
                </form>
            </div>
        </section>
    )
}

export default Edit;