const ACTIONS = {
    LOAD_MOVIE_ARTICLES: 'load-movie-articles',
    MODIFY_MOVIE_ARTICLE_COMMENT: 'modify-movie-article-comment',
    GET_MOVIE_ARTICLE: 'get-movie-article',
    GET_MOVIE_LIKES_FROM_USER: 'get-movie-likes-from-user',
    SET_IS_LIKED_TO_TRUE: 'set-is-liked-to-true',
    DELETE_CURRENT_MOVIE: 'delete-current-movie',
    CANCLE_DELETE_MOVE: 'cancel-delete-movie',
}

function reducerFunction(state, action) {
    switch (action.type) {
        case ACTIONS.LOAD_MOVIE_ARTICLES:
            return {...state, movieArticleComments: action.payload, areCommentsLoading: false, isFormSubmited: false }
        case ACTIONS.MODIFY_MOVIE_ARTICLE_COMMENT:
            return {...state, isFormSubmited: true }
        case ACTIONS.GET_MOVIE_ARTICLE:
            return {...state, movieArticle: action.payload, isMovieLoading: false }
        case ACTIONS.GET_MOVIE_LIKES_FROM_USER:
            return {...state, movieArticleLikesFromUser: action.payload }
        case ACTIONS.SET_IS_LIKED_TO_TRUE:
            return {...state, isLiked: true }
        case ACTIONS.DELETE_CURRENT_MOVIE:
            return {...state, deleteMovieArticleConfirmation: 'Are u sure u want to delete this movie article?!' }
        case ACTIONS.CANCLE_DELETE_MOVE:
            return {...state, deleteMovieArticleConfirmation: null }
        default:
            return state;
    }
}

export default reducerFunction;