import { useEffect, useReducer } from 'react';
import style from './Details.module.css';

import CommentSection from './CommentSection/CommentSection';
import DetailsButtons from './DetailsButtons/DetailsButtons';

import { getSpecificMovieArticle } from '../../../utils/MainRequests';
import reducerFunction from './DetailsReducer/DetailsReducer';
import Loader from '../../Common/Loader/Loader';


function Details({
    match,
}) {
    const movieId = match.params.movieId;
    const defaultState = {
        movieArticle: {},
        movieArticleComments: [],
        areCommentsLoading: true,
        isFormSubmited: false,
        numberOfLikes: 0,
        movieArticleLikesFromUser: [],
        isLiked: false,
        deleteMovieArticleConfirmation: null,
        isMovieLoading: true
    }
    const [currentState, dispatch] = useReducer(reducerFunction, defaultState);

    useEffect(() => {
        (async function getMovieArticleByHisId() {
            dispatch({
                type: 'get-movie-article',
                payload: await getSpecificMovieArticle(movieId),
            })
        })();
    }, [movieId]);

    return (
        <>
            {currentState.isMovieLoading
                ? <Loader />
                : <section id="details-view">
                    <div className={style.movieContainer}>
                        <h1 className={style.movieDetailsTitle}>Movie title: {currentState.movieArticle.title}</h1>
                        <img className={style.movieDetailsImg} src={currentState.movieArticle.img} alt="movie" />
                        <div className={style.movieDetailsInfo}>
                            <h3 className={style.movieDetailsInfoImg}>Movie Description</h3>
                            <p className={style.movieDetailsInfoParagraph}>
                                {currentState.movieArticle.description}
                            </p>
                            <DetailsButtons
                                dispatch={dispatch}
                                movieId={movieId}
                                ownerId={currentState.movieArticle._ownerId}
                                state={currentState}
                            />
                            <CommentSection
                                dispatch={dispatch}
                                movieId={movieId}
                                ownerId={currentState.movieArticle._ownerId}
                                state={currentState}
                            />
                        </div>
                    </div>
                </section>
            }
        </>
    );
}


export default Details;